<script>
import { reactive, toRefs } from "vue";
import sidebar from "@/components/Sidebar";
export default {
  components: { sidebar },
  setup() {
    const data = reactive({
      show: false,
      shows: () => {
        data.show = true;
      },
      showfun: () => {
        data.show = false;
      }
    });
    const refDate = toRefs(data);
    return { ...refDate };
  }
};
