    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { setLang } from "@/utils/i18n";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import emitter from '../bus/bus.js'
export default {
  setup(props, context) {
    const router = useRouter();
    const store = useStore();
    const data = reactive({
      activeMenu:store.state.activeMenu,
      numtype: "",
      local: computed(() => store.state.locale),
		  pagesabout: computed(() => store.state.pagesabout),
      event: type => {
        if (data.numtype === type) {
          return (data.numtype = "");
        }
        data.numtype = type;
      },
      changeLanguage: inject => {
        setLang(inject);
        router.go(0);
      },
      toSwap: () => {
        window.location.href = "/swap";
      },
      hide: () => {
        context.emit("showfun");
      },
	  goDetail() {
	     
	      },

	  gotoLike: (e,name) => {
      console.log(name)
		  // pagesabout.set('999');
		  // store.commit("updatePageaboutMode", '999');
      emitter.emit('param', { pagesUrl: e })
      store.commit("updateactiveMenu", name);
      data.activeMenu = name
      // Utils.$emit('demo','msg');
		  // store.commit("statechange", {
		  //     ways: 'menuRouter',
		  //     data: 888
		  //   })
		  // router.push({
		  //   path: "/",
		  //   query: {
		  //     page: 'about',
		  //   },
		  // });
		//锚点跳转
		 // router.push('index/?name=')
		// console.log( router.push('index/?name='));return
		// let that = this
		// that.$router.push('index/?name=')
		// var id = "#" + event;
		// console.log(document.querySelector(id))
		// document.querySelector(id).scrollIntoView({
		//   behavior: "smooth",
		//   block: "center",
		//   inline: "nearest",
		// });
	  },
    });
    const refDate = toRefs(data);
    return { ...refDate };
  }
};
</script>

