.package {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background: rgba($color: #000000, $alpha: 0.6);
  z-index: 1111;
  .rigt {
    position: absolute;
    width: 56%;
    height: 100%;
    right: 0;
    background: #0B1D33;
    padding: 57px 0 20px;
    overflow: auto;
    .cou {
      position: absolute;
      height: 57px;
      padding: 0 17px;
      position: absolute;
      right: 0;
      top: 0;
      img {
        width: 13px;
        height: 13px;
      }
    }
    .warp {
      .item {
        height: 48px;
        padding: 0 0 0 18px;
        .item-icon {
          width: 19px;
          height: 19px;
          &:nth-child(1) {
            display: none;
          }
        }
        .title {
          font-size: 15px;
          color: #fff;
          font-weight: bold;
          margin-left: 14.5px;
        }
      }
      .active {
        background: #030B15;
        .item-icon {
          &:nth-child(1) {
            display: block;
          }
          &:nth-child(2) {
            display: none;
          }
        }
      }
    }
  }
}
.right {
