<script>
import { reactive, toRefs, computed, onMounted, watch } from "vue";
import { useStore } from "vuex";
import IDOAbi from "@/utils/idoAbi";
import tokenAbi from "@/utils/tokenAbi";
import { config } from "@/utils/config";
import { useI18n } from "vue-i18n";
import emitter from "../../bus/bus.js";
import Progress from "@/components/Progress";
import { useCopyToClipboard } from "@/components/useCopyToClipboard.js";
import web3Data from "@/utils/initWeb3";
import { ElMessage } from "element-plus";
import BigNumber from "bignumber.js";
import { MaxUint256 } from "@ethersproject/constants";
export default {
  components: { Progress },
  setup() {
    const store = useStore();
    const { t } = useI18n();
    onMounted(() => {
      var url = window.location.href;
      if (url.indexOf("?") != -1) {
        var str = url.substr(url.indexOf("?"));
        var strs = str.split("=");
        console.log("strs:", strs);
        store.commit("updatePromotionUrl", strs[1]);
      }
      console.log("获取推荐人地址=" + store.state.promotionUrl);
    });
    emitter.on("param", (data) => {
      console.log(data);
      document.querySelector("#" + data.pagesUrl).scrollIntoView({
        behavior: "smooth",
        block: "center",
        inline: "nearest",
      });
    });
    const data = reactive({
      BigNumber: BigNumber,
      web3: computed(() => store.state.provider),
      address: computed(() => store.state.accounts[0]),
      // InviteLink: "baidu.com",
      poolAddress: config.poolAddress,
      money: "",
      pendingMafa: 0,
      mafaDecimals: 0,
      Inviteraddress: "",
      promotionLink: "",
      recommendedNumber: 0,
      rewardAmount: 0,
      directPush: 0,
      progressNum:10,
      Interposition: 0,
      accumulatedAmount: 0,
      totalAmount: 0,
      USDTContract: null,
      modeInfo: null,
      userData: null,
      balance: 0,
      allowance: 0,
      amount: "",
      USDTAmount: "",
      teamNumber: 0,
      claimed: false,
      loading: false,
      minCont: 200,
      maxCont: 500,
      initWallet: () => {
        if (!data.address) {
          web3Data.initWeb3();
        }
      },
      pay: (e) => {
        if (!Number(data.money)) {
          ElMessage.error(t("請輸入購買數量"));
          return;
        }
        let amount = new BigNumber(data.money)
          .multipliedBy(Math.pow(10, 18))
          .toFixed();
        if (new BigNumber(data.balance).lt(amount)) {
          ElMessage.error(t("餘額不足"));
          return;
        }
        console.log("address", data.address);
        data.loading = true;
        let referAddress = store.state.promotionUrl;
        console.log("referAddress",config.zeroAddress)
        if (!referAddress) {
          referAddress = config.ZeroAddress;
        }
        data.idoContract.methods
          .contribute(referAddress, amount)
          .estimateGas({ from: data.address })
          .then((gas) => {
            data.idoContract.methods
              .contribute(referAddress, amount)
              .send({ from: data.address, gas: gas })
              .then(() => {
                ElMessage.success(t("交易成功"));
                data.loading = false;
                data.amount = "";
                data.initData();
              })
              .catch(() => {
                data.loading = false;
                ElMessage.error(t("交易失敗"));
              });
          })
          .catch((err) => {
            console.log(err);
            data.loading = false;
            console.log(err);
            data.loading = false;
            let errStr = err.toString();
            if (errStr.includes("live")) {
              ElMessage.error("IDO no going");
            } else if (errStr.includes("Ido ended")) {
              ElMessage.error("Ido ended");
            } else if (errStr.includes("less than min amout")) {
              ElMessage.error("Less than min amout");
            } else if (errStr.includes("Exceeds max amount")) {
              ElMessage.error("Exceeds max amount");
            } else {
              ElMessage.error(errStr);
            }
          });
      },
      receive: (e) => {
        console.log("领取 receive");
        if (new BigNumber(data.pendingMafa).lt(0)) {
          ElMessage.error(t("餘額不足"));
          return;
        }
        data.loading = true;
        data.idoContract.methods
          .claim()
          .estimateGas({ from: data.address })
          .then((gas) => {
            data.idoContract.methods
              .claim()
              .send({ from: data.address, gas: gas })
              .then(() => {
                ElMessage.success(t("成功"));
                data.loading = false;
                data.amount = "";
                data.initData();
              })
              .catch(() => {
                data.loading = false;
                ElMessage.error(t("失敗"));
              });
          })
          .catch((err) => {
            console.log(err);
            data.loading = false;
            let errStr = err.toString();
            if (errStr.includes("Claim not open yet")) {
              ElMessage.error("Claim not open yet");
            }
            if (errStr.includes("No pending amount")) {
              ElMessage.error("No pending amount");
            }
            if (errStr.includes("Claimed")) {
              ElMessage.error("Claimed");
            }
          });
      },
      copyLikn: (e) => {
        console.log("复制链接 copyLikn");
        let { isSuccessRef } = useCopyToClipboard(data.promotionLink);
        if (isSuccessRef) {
          ElMessage.success(t("复制成功"));
        } else {
          ElMessage.error(t("复制失败"));
        }
      },
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(
          IDOAbi,
          config.IDOAddress
        );
        data.USDTContract = new data.web3.eth.Contract(
          tokenAbi,
          config.USDTAddress
        );
        data.mafaContract = new data.web3.eth.Contract(
          tokenAbi,
          config.MAFAAddress
        );
        var url = window.location.href;
        if (url.indexOf("?") != -1) {
          url = url.substr(0,url.indexOf("?"));
        }
        data.promotionLink =
          url + "?promotion=" + data.address;
        data.initData();
      },
      initData: () => {
        data.getInfo();
        data.getBalance();
        data.getAllowance();
        data.getReferer();
      },
      getBalance: async () => {
        let result = await data.USDTContract.methods
          .balanceOf(data.address)
          .call();
        data.balance = result;
      },
      getReferer: async () => {
        let result = await data.idoContract.methods
          .getRefer(data.address)
          .call();
        data.Inviteraddress = result;
      },
      gotoLink: (e) => {
        emitter.emit("param", { pagesUrl: e });
      },
      getInfo: async () => {
        let result = await data.idoContract.methods.getIdoInfo().call();
        data.modeInfo = { ...result };
        console.log(data.modeInfo);
        let res = await data.idoContract.methods
          .getUserInfo(data.address)
          .call();
        data.userData = { ...res };
        console.log(data.userData);
                data.minCont = new BigNumber(data.modeInfo._min)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.maxCont = new BigNumber(data.modeInfo._max)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.teamNumber = await data.idoContract.methods
          .getTeamNumber(data.address)
          .call();
        data.rewardAmount = new BigNumber(data.userData._rewardAmount)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.accumulatedAmount = new BigNumber(data.modeInfo._contributedAmount)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.totalAmount = new BigNumber(data.modeInfo._hardcap)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.progressNum = (data.accumulatedAmount*100)/data.totalAmount;
        data.USDTAmount = new BigNumber(data.userData._contributed)
          .dividedToIntegerBy(Math.pow(10, 18))
          .toFixed();
        data.mafaDecimals = await data.mafaContract.methods.decimals().call();
        data.pendingMafa = new BigNumber(data.userData._mafaAmount)
          .dividedToIntegerBy(Math.pow(10, data.mafaDecimals))
          .toFixed();
        console.log("mafa decimals", data.pendingMafa);
        data.claimed = data.userData.claimed;
      },
      getAllowance: async () => {
        data.allowance = await data.USDTContract.methods
          .allowance(data.address, config.IDOAddress)
          .call();
      },
      approve: () => {
        data.loading = true;
        data.USDTContract.methods
          .approve(config.IDOAddress, MaxUint256)
          .send({ from: data.address })
          .then((result) => {
            data.loading = false;
            data.getAllowance();
          })
          .catch(() => {
            data.loading = false;
          });
      },
    });
    watch(
      () => data.address,
      (val) => {
        if (val) {
          data.initContract();
        }
      }
    );
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
};
</script>
