    opacity: 0;
    position: absolute;
    top: 56px;
    height: 3px;
    background: #0956d0;
    transition: all 0.3s;
    pointer-events: none;
    &::before {
      content: "";
      position: absolute;
      top: -3px;
      left: 0;
      right: 0;
      margin: 0 auto;
      width: 10px;
      border-bottom: 4px solid #0956d0;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
    }
  }
  .menu-item {
    padding: 0 30px;
    font-size: 16px;
    color: #fff;
    height: 60px;
    line-height: 60px;
    white-space: nowrap;
    .el-icon-arrow-down {
      display: inline-block;
    }
    .el-icon-arrow-up {
      display: none;
    }
    cursor: pointer;
    &.menu-item--active {
      color: #0956d0;
      .el-icon-arrow-down {
        display: none;
      }
      .el-icon-arrow-up {
        display: inline-block;
      }
    }
    &:hover {
      color: #0956d0;
      .el-icon-arrow-down {
        display: none;
      }
      .el-icon-arrow-up {
        display: inline-block;
      }
    }
    i {
      font-size: 12px;
      pointer-events: none;
    }
  }
  .sub-menu {
    position: relative;
    .sub-menu--box {
      display: none;
      min-width: 100%;
      position: absolute;
      top: 56px;
      left: 50%;
      transform: translateX(-50%);
      background: #00012f;
      border-top: 3px solid #0956d0;
      max-height: 300px;
      overflow: auto;
      &::before {
        content: "";
        position: absolute;
        top: -7px;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 10px;
        border-bottom: 4px solid #0956d0;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
      }
      .menu-item {
        padding: 0 30px;
        text-align: center;
        white-space: nowrap;
        &:hover {
          background: rgba(9, 86, 208, 0.2);
        }
      }
      .menu-item + .menu-item {
        // border-top: 1px solid #0956d0;
      }
    }
    &:hover {
      & > .menu-item {
        color: #0956d0;
      }
      .sub-menu--box {
        display: block;
      }
      .el-icon-arrow-down {
        display: none;
      }
      .el-icon-arrow-up {
        display: inline-block;
      }
    }
  }
}
