import { createStore } from "vuex";

const setStorage = (key, value) => {
  sessionStorage.setItem(key, value);
};

// 本地获取
const getStorage = item => {
  let str = sessionStorage.getItem(item);
  return str;
  }

export default createStore({
  state: {
    accounts: [],
    provider: null,
    TronAccounts: '',
    TronProvider: null,
    netId: '',
    locale: '',
    themeMode: '',
    menuRouter:'',
    provider: null,
    promotionUrl:'',
    activeMenu:'Index',
  },
  mutations: {
    updateAccount(state, value) {
      state.accounts = value;
    },
    updateProvider(state, value) {
      state.provider = value;
    },
    updateTronAccount(state, value) {
      state.TronAccounts = value;
    },
    updateTronProvider(state, value) {
      state.TronProvider = value;
    },
    updateNetId(state, value) {
      state.netId = value;
    },
    updateLocale(state, value) {
      state.locale = value;
    },
    updateThemeMode(state, value) {
      state.themeMode = value;
    },
    updatePromotionUrl(state, value) {
      state.promotionUrl = value;
    },
    updateactiveMenu(state, value) {
      state.activeMenu = value;
    },
	statechange(state, r) {
	      if (state.hasOwnProperty(r.ways)) {
	        state[r.ways] = r.data;
	        setStorage(r.ways, r.data);
	      }
	}
  },
  actions: {},
  modules: {}
});
