import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../views/index";

const routes = [
  {
    path: "/",
    name: "Index",
	meta: {
	 keepAlive: false, //此组件不需要被缓存
	 isBack: false, //用于判断上一个页面是哪个
	 },
    component: Index
  },
  {
    path: "/about/index",
    name: "About",
    component: () => import("../views/about")
  },
  {
    path: "/features/index",
    name: "Features",
    component: () => import("../views/features")
  },
  {
    path: "/tokens/index",
    name: "Tokens",
    component: () => import("../views/tokens")
  },
  {
    path: "/roadmap/index",
    name: "Roadmap",
    component: () => import("../views/roadmap")
  },
  {
    path: "/address/list",
    name: "AddressList",
    component: () => import("../views/addressList")
  },
  {
    path: "/pledge/:type/:name/:token",
    name: "Pledge",
    component: () => import("../views/pledge")
  },
  {
    path: "/withdraw/:type/:name/:token",
    name: "Withdraw",
    component: () => import("../views/withdraw")
  },
  {
    path: "/ido",
    name: "Ido",
    component: () => import("../views/ido")
  },
  {
    path: "/block/address/detail/:address",
    name: "AddressDetail",
    component: () =>
      import(
        /* webpackChunkName: "AddressDetail" */ "../views/block/address/detail"
      )
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
